@import "../../../assets/variables";

.winning-wrapper {
  height: 100vh;
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F5F5F7;
}

.winning-container {
  position: relative;
  bottom: 75px;
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) and (max-width: 1024px){
    bottom: 45px;
  }

  @media screen and (min-width: 577px) and (max-width: 767px) {
    bottom: 30px;
  }

  @media screen and (max-width: 576px) {
    display: flex;
    flex-direction: column;
    bottom: 10px;
  }

  &__image-box {
    padding-right: 32px;

    @media screen and (min-width: 577px) and (max-width: 767px) {
      padding-right: 0;
    }

    @media screen and (max-width: 576px) {
      padding-right: 0;
      padding-bottom: 24px;
    }

    &__picture {
      min-width: 288px;

      @media screen and (min-width: 577px) and (max-width: 767px) {
        max-width: 288px;
      }
    }
  }
}

.content {
  width: 100%;
  max-width: 520px;

  @media screen and (max-width: 576px) {
    text-align: center;
  }

  &__title {
    font-family: $main-font;
    font-weight: 600;
    font-size: 32px;
    line-height: 37px;
    color: $dark-color;
    opacity: 0.5;
    padding-bottom: 8px;

    @media screen and (max-width: 576px) {
      text-align: center;
    }

    @media screen and (max-width: 480px) {
      font-size: 18px;
      line-height: 23px;
    }
  }

  &__subtitle {
    font-family: $main-font;
    font-weight: 600;
    font-size: 56px;
    color: $dark-color;
    padding-bottom: 64px;

    @media screen and (min-width: 768px) and (max-width: 1024px){
      font-size: 41px;
      line-height: 50px;
      padding-bottom: 45px;
    }

    @media screen and (min-width: 577px) and (max-width: 767px) {
      font-size: 32px;
      line-height: 41px;
      padding-bottom: 30px;
    }

    @media screen and (max-width: 576px) {
      font-size: 35px;
      line-height: 43px;
      padding-bottom: 50px;
      text-align: center;
    }

    @media screen and (max-width: 480px) {
      font-size: 32px;
      line-height: 37px;
      padding-bottom: 100px;
    }
  }
}